body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #FFFFFF;
}

* {
  font-family: 'Inter', system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  color: #272729;
}

h3 {
  font-weight: 400;
  font-size: 16px;
}

header {
  border-bottom: 1px solid #D3D3D3;
}

.wrapper {
  background: #FFFFFF;
  // box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);

  max-width: 1080px;
  margin: 0 auto;
}

.content {
  h1 {
    margin: 0;
  }
}

.button {
  background: #FFFFFF;
  border: 1px solid #f2f2f2;
  box-sizing: border-box;
  border-radius: 8px;
  height: 32px;
  width: 32px;
  cursor: pointer;
}

.overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color:rgba(0, 0, 0, 0.5);
  z-index: 1;
}

.video {
  width: 519px;
  height: 519px;
  border-radius: 50px; 
  overflow: hidden; 
}

.objection519 {
  width: 519px;
  height: 519px;

  background-color: #FBFBFD;
  border-radius: 50px;
  font-weight: 550;

  h1 {
    margin: 34px auto;
    width: 80%;
    text-align: center;
    font-size: 38px;
    font-weight: 500;
  }

  p {
    font-size: 20px;
    margin: 0 80px;
  }

  span {
    color: #6F6F6F;
  }
}